.faqs-section {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .partners {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1rem;
    margin: 60px -100px 0;
  }
  
  .sponsors {
    color: var(--Main-Green-color, #1FD891);
    font-family: Poppins;
    font-size: 24px;
    font-weight: 700;
  }
  
  .faqs {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 150px;
    gap: 1rem;
  }
  
  .section-title {
    color: var(--Main-Green-color, #1FD891);
    font-family: Poppins;
    font-size: 60px;
    font-weight: 700;
    margin-bottom: 50px;
    text-align: center;
  }
